  .backToTop{
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 50px;
    right: 80px;
  }
  .backToTop img{
  	width: 100%;
  	height: 100%;
  	cursor: pointer;
  }
/*封面*/
	.cover{
		width: 100%;
		height: 500px;
		position: relative;
	}
	.cover img{
		width: 100%;
		height: 100%;
	}
	.pictures{
		height: 32px;
		width: 80px;
		font-size: 14px;
		text-align: center;
		line-height: 32px;
		border-radius: 4px;
		background: #FFDE3A;
		position: absolute;
		bottom: 28px;
		left: 50px;
		z-index: 10;
		cursor: pointer;
	}

.main{
	width: 1200px;
	/*height: 800px;*/
	margin: 0 auto;
	position: relative;
}
.main_left{
	width: 70%;
	/*height: 800px;*/
	/*background: rgba(188,198,221,0.7);*/
	float: left;
}
/*定位*/
	.located{
		width: 100%;
		height: 60px;
	}
	.located ul{
		width: 100%;
		height: 60px;
		/*margin-left: 3%;*/
		border-bottom: 2px solid #ccc;
	}
	.located ul li{
		/*width: 8%;*/
		height: 60px;
		float: left;
		/*text-align: center;*/
		line-height: 70px;
		cursor: pointer;
		margin-left: 30px;
		border-bottom: 2px solid #ccc;
	}
/*名称栏*/
	.namecon{
		width: 100%;
		border-bottom: 2px solid #ccc;
	}
	.na_sc{
		height: 70px;
		width: 100%;
		margin-top: 10px;
	}
	.spo_name{
		height: 70px;
		font-size: 20px;
		line-height: 70px;
		float: left;
	}
	.spo_score{
		height: 70px;
		width: 30%;
		float: right;
	}
	.spo_score ul li{
		float: left;
		height: 70px;
		line-height: 70px;
		margin-right: 15px;
	}
	.spo_score div{
		float: left;
		height: 70px;
		line-height: 70px;
		color: #505050;
	}
	.short_addr{
		width: 100%;
		height: 30px;
		line-height: 10px;
		margin-bottom: 20px;
		font-size: 14px;
		color: #505050;
	}
/*简介与联系方式与详细地址*/
	.introduce{
		margin: 30px 0px 20px;
		width: 100%;
		height: 200px;
		/*background: rgba(188,198,121,0.7);*/
	}
	.contact{
		width: 100%;
	}
	.address{
		margin: 30px 0px 20px;
		width: 100%;
	}
	.introduce ul,.contact ul,.address ul{
		width: 100%;
	}
	.introduce ul li:nth-child(1),.contact ul li:nth-child(1),.address ul li:nth-child(1){
		width: 12%;
		height: 20px;
		line-height: 20px;
		margin-right: 2%;
		float: left;
		text-align: right;
	}
	.introduce ul li:nth-child(2),.contact ul li:nth-child(2),.address ul li:nth-child(2){
		width: 82%;
		float: left;
		line-height: 20px;
	}
/*视频*/
	.spo_video{
		width: 100%;
		height: 400px;
		margin: 30px 0px 40px;
	}
	.hengg1{
		height: 2px;
		width: 100%;
		background: #ccc;
	}
/*位置显示*/
	.map{
		width: 100%;
		height: 450px;
		background: rgba(188,198,221,0.8);
		margin-bottom: 40px;
	}
/*右侧*/
	.main_right{
		width: 25%;
		height: 800px;
		/*background: rgba(188,198,121,0.7);*/
		top: -60px;
		right: 0px;
		position: absolute;
	}
	.price{
		background: rgba(0,1,1,0.6);
		width: 100%;
		height: 60px;
		text-align: center;
		line-height: 60px;
		color: #fff;
		font-size: 16px;
	}
	.support{
		width: 100%;
		height: 200px;
		border: 1px solid #bfbfbf;
	}
	.sup_btn{
		width: 70%;
		height: 40px;
		text-align: center;
		line-height: 40px;
		margin: 44px auto 20px;
		border: 1px solid #bfbfbf;
		border-radius: 3px;
		cursor: pointer;
	}
	.sup_btn img{
		margin-right: 10px;
	}
	.sup_count{
		text-align: center;
		height: 30px;
		line-height: 26px;
		width: 100%;
		font-size: 14px;
	}
	.share{
		height: 40px;
		line-height: 40px;
		width: 100%;
		text-align: center;
	}
	.share img{
		margin-right: 22px;
		cursor: pointer;
	}
	.wrong{
		width: 100%;
		height: 50px;
		margin-top: 30px;
		line-height: 50px;
		text-align: center;
		border: 1px solid #bfbfbf;
		cursor: pointer;
	}
	.wrong img{
		margin-right: 10px;
	}
.main_right img,.stars img,.one_rem img,.spo_score img{
	vertical-align: middle;
}


