.midr .midr2 .btn1{
    float: right;margin-right: 30px;
}
.midr .midr2 .cell{
    width: 100%;
    height: 150px;
    margin-top: 30px;
    margin-bottom: 10px;
    float: left;
}
.midr .midr2 .cell img{
    float: left;
    width:33%;
    height:100%;
    margin-right: 5%;
}
.midr .midr2 .cell .cellr{
    float: left;
    width: 50%;
    height: 100%;
    margin-right: 5%;
}
.midr .midr2 .cell .cellr>a{
    display: block;
    width: 100%;
    padding-right: 5%;
    box-sizing: border-box;
}
.midr .midr2 .cell .cellr>a h4{
    width:100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.midr .midr2 .cell .cellr p{
    margin-top: 20px;
}


