/*评论*/
	.stars{
		width: 94%;
		height: 80px;
		margin: 0 auto;
	}
	.stars ul{
		float: left;
		height: 80px;	
	}
	.stars li{
		float: left;
		height: 80px;
		line-height: 80px;
		margin-left: 10px;
	}
	.stars li img{
		cursor: pointer;
		margin-left: 5px;
		vertical-align:middle;
	}
	.stars div{
		float: right;
		width: 60px;
		height: 80px;
		/*margin-right: 30px;*/
		line-height: 80px;
	}
	.stars div span{
		color: red;
		font-size: 20px;
		margin-right: 10px;
	}
	.remark_textarea{
		width: 95%;
		margin: 0px 1.3% 20px;
		height: 240px;
		line-height: 20px;
		border-radius: 4px;
		border: 1px solid #000;
		padding: 9px 12px;
	}
	.remark_sub{
		width: 7.5%;
		height: 35px;
		background: #FFDE3A;
		text-align: center;
		border-radius: 3px;
		border: 1px solid #FFDE3A;
		float: right;
		cursor: pointer;
		margin: 0px 50px 35px 0px;
	}
	.remark_con{
		width: 97%;
		margin: 0 auto; 
	}
	.one_rem{
		margin-bottom: 30px;
	}
	.remark_infor{
		height: 70px;
		width: 100%;
	}
	.user_hp{
		height: 70px;
    	width: 70px;
    	float: left;
	}
	.user_hp img{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		vertical-align:middle;
	}
	.na_time{
		width: 20%;
		height: 70px;
		float: left;
		margin-left: 10px;
	}
	.user_na{
		height: 40px;
		line-height: 55px;
		font-weight: 600;
	}
	.time_re{
		height: 30px;
		line-height: 20px;
		color: #999999;
		font-size: small;
	}
	.like_dis{
		width: 190px;
		float: right;
		height: 70px;
	}
	.like_dis li{
		float: left;
		height: 70px;
		line-height: 100px;
		color: #BCBCBC;
	}
	.like_dis li:nth-child(2),.like_dis li:nth-child(4){
		margin-left: 7px;
	}
	.like_dis li img{
		font-size: 25px;
		margin-left: 30px;
		cursor: pointer;
		vertical-align:middle;
	}
	.remark_text{
		width: 95%;
		background: #EBEBEB;
		border-radius: 8px;
		padding: 20px 26px;
		margin: 10px 0px;
	}
	.or_con{
		line-height: 25px;
	}
	.hengg2{
	  height: 1px;
		margin: 0 auto;
		background: #ccc;
		margin: 20px 0px;
	}
	.or_score ul{
		float: left;
	}
	.or_score ul li{
		float: left;
		margin-left: 10px; 
	}
	.or_score span{
		float: right;
		margin-right: 1%;
	}
	.pages{
		width: 100%;
		height: 50px;
		margin: 40px 0px;
	}
	.pages li{
		float: left;
		margin-left: 1%;
		height: 34px;
		width: 34px;
		border: 1px solid #999999;
		border-radius: 4px;
		text-align: center;
		line-height: 31px;
		color: #999999;
		cursor: pointer;
	}
textarea,input{
	font-family: '微软雅黑';
	font-size: 16px;
}


