body {
    width: 100%;
    height: 100%;
    font-family: '微软雅黑';
}

ul, li, html, body, h2, span {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.clear {
    clear: both;
}

.clear:after {
    content: "";
    clear: both;
    height: 0;
    display: block;
}

.aFont{
  color: #000;
  text-decoration: none;
}

a{
  text-decoration: none;
  color: #000;
}

.footer{
  width: 100%;
  height: 120px;
  background: #000;
  color: #fff;
  text-align: center;
  line-height: 120px;
}



