

/*顶部*/
.topbar {
  background: url(./../images/top-bg.jpg) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 320px;
}

.top {
  width: 100%;
}

.top1 {
  height: 70px;
}

.top1 p {
  font-size: 36px;
  font-weight: 700;
  color: #FFDE3A;
  margin-left: 50px;
  height: 70px;
  line-height: 70px;
}

.top2 {
  height: 170px;
}

.top2 img {
  width: 150px;
  height: 150px;
  height: 150px;
  border-radius: 50%;
  margin-left: 100px;
  margin-top: 70px;
}

.top3 {
  width: 100%;
  height: 80px;
  background: rgba(0, 0, 0, 0.4);
  color: #ffffff;
}

.top3 .top3l {
  margin-left: 270px;
  height: 80px;
  width: 30%;
  float: left;
}

.top3 .top3l .username {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 24px;
  font-weight: 700;
}

.top3 .top3l p {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 15px;
}

.top3 .top3r {
  height: 80px;
  width: 30%;
  float: right;
  font-size: 15px;
}

.top3 .top3r p {
  height: 40px;
  line-height: 40px;
  margin-top: 40px;
}

.top3 .top3r p span {
  height: 40px;
  line-height: 40px;
  margin-right: 20px;
}

/*内容*/
.midbar {
  width: 1200px;
  margin: 40px auto;
  height: auto;
  min-height: 500px;
}

.midbar .mid1 {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: large;
  border-bottom: 2px #999 solid;
}

.midbar .midl {
  width: 22%;
  height: auto;
  float: left;
}

.midbar .midl .midl2 {
  width: 55%;
  height: auto;
  min-height: 200px;
  margin: 0 auto;
  margin-bottom: 80px;
}

.midbar .midl .midl2 ul {
  width: 100%;
  list-style: none;
}

.midbar .midl .midl2 ul li {
  width: 100%;
  height: 70px;
  line-height: 70px;
  text-align: center;
  border-bottom: 1px solid #999;
  font-size: large;
}

.midbar .midl .midl2 ul li:nth-child(7){
  border-bottom: 1px solid #fff;
}

.midbar .midl .midl2 ul li .active {
  color: #FFDE3A;
  font-weight: bold;
}

.midbar .midr {
  width: 78%;
  height: auto;
  min-height: 500px;
  float: left;
  margin-bottom: 50px;
}

.midbar .midr .midr1 span {
  width: auto;
  margin-right: 30px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
}

.midbar .midr .midr1 .active {
  border-bottom: 3px solid #FFDE3A;
}

.midbar .midr .content {
  width: 100%;
  height: auto;
  min-height: 400px;
  float: left;
  padding-top: 20px;
}

.footer{
  width: 100%;
  height: 120px;
  background: #000;
  color: #fff;
  text-align: center;
  line-height: 120px;
}





