/*模态框*/
.modal{
    position: fixed;
    top:0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: rgba(0,0,0,0.7);
  }
  .modal-con{
    position: fixed;
    top: 50%;
    right: 50%;
    transform:translate(50%,-50%);
    width: 1000px;
    height: 80%;
    background: whitesmoke;
    box-shadow: rgba(76,76,76,0.7) 0px 0px 10px 5px;
    border-radius:2px;
    /* overflow-x: scroll; */
  }
/* 图片模态框 */
  .modal-con-noColor{
    /*height: 500px;*/
    height: 80%;
    background: rgba(0,0,0,0);
    width: auto;
  }
  .modal-con-img{
    /*height: 500px;*/
    height: 100%;
    background: rgba(0,0,0,0);
    clear: both;
    position: relative;
    text-align: center;
  }
  .btn-close{
    position: absolute;
    right: -30px;
    top: -30px;
  	background: url(../icons/close1.png) no-repeat center;
  	width: 30px;
  	height: 30px;
  	border: 1px solid rgba(1,1,1,0);
    cursor: pointer;
  	z-index: 10;
    outline:none;
  }
  .modal-con-img img{   /* 相册里的图片 */
    width: auto;
    min-width: 200px;
    height: 100%;
    /*position: absolute;*/
    top: 0px;
    right: 0px;
  }
  .modal-img-btn{
    width: 100px;
    height: 100%;
    background: rgba(255,0,0,0.4);
    position: absolute;
    opacity: 0;
  }
  .modal-img-btn-left{
    top: 0px;
    left: 0px;
    background: url(../icons/btn-left.png) no-repeat center;
    background-size: 50px;
    cursor: pointer;
  }
  .modal-img-btn-right{
    top: 0px;
    right: 0px;
    background: url(../icons/btn-right.png) no-repeat center;
    background-size: 50px;
    cursor: pointer;
  }
  .showBtnClass{
    opacity: 1;
  }
  .imgName{
    width: 100%;
    position: absolute;
    bottom: 0px;
    height: 40px;
    line-height: 40px;
    background: rgba(50,50,50,0.5);
  }

  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to{
    /* .slide-fade-leave-active for below version 2.1.8 */
    transform: translateX(10px);
    opacity: 0;
  }
/* 文本模态框 */
  .modal-head{
    width: 100%;
    height:10%;
    line-height: 60px;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #000;
  }
  .model-time{
    width: 100%;
    height: 30px;
    line-height: 35px;
    font-size: 14px;
    text-align: center;
    color: #8a8a8a;
  }
  .modal-body{
    width: 100%;
    height: 80%;
    box-sizing: border-box;
    padding: 10px 40px;
    overflow-y: auto;
    text-align: center;
  }
  .modal-body p{
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
    word-break:break-all;
    text-align: left;
  }
  .modal-body img{    /* 文章里的图片 */
    width: 80%;
    height: auto;
  }
  .modal-foot{
    width: 100%;
    height: 10%;
    text-align: right;
    padding-right: 40px;
    padding-top: 10px;
    box-sizing: border-box;
  }
/*视频模态框*/
  .modal-paly{
    width: 100%;
    height: 90%;
    /*margin: 0px auto;*/
  }
  .modal-title{
    width: 100%;
    height:10%;
    line-height: 60px;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #fff;
  }
  .modal-con-video{
    height: 80%;
    background: rgba(0,0,0,0);
    width: 930px;
    box-shadow: rgba(0,0,0,0) 0px 0px 10px 5px;
  }


